import React, { Component } from 'react';

import NavBar from '../components/navbar';
import BestSellerGrid from "../components/bestseller-grid";
import Footer from '../components/footer';
import MainButtonBar from "../components/main-button-bar";

import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';

import mainImage1 from '../assets/main-image-1.jpg';
import mainImage2 from '../assets/main-image-2.jpg';

import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';

import './mainpage.css';

export class MainPage extends Component {
    render() {
        return (
            <div>
                <NavBar />
                <div className={"Slider"}>
                    <AwesomeSlider bullets={false} >
                        <div className={"Slide"}>
                            <img src={mainImage1} className={"SlideImage"} />
                            <div className={"Slide1Mask"}>
                                <h1 className={"Slide1Header"}>Aesthetic</h1>
                                <h3 className={"Slide1Subtitle"}>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</h3>
                            </div>
                        </div>
                        <div className={"Slide"}>
                            <img src={mainImage2} className={"SlideImage"} />
                            <p className={"Slide2Text"}>Make Appointment now.</p>
                            <a href={"#"}><div className={"Slide2Button"}>MAKE APPOINTMENT</div></a>
                        </div>
                    </AwesomeSlider>
                </div>
                <MainButtonBar />
                <div className={"BestSellers"}>
                    <h1 className={"BestSellerHeader"}>BEST SELLERS</h1>
                    <div className={"BestSellerHeaderBar"} />
                    <BestSellerGrid />
                </div>
                <div className={"VisitUsContainer"}>
                    <div className={"ContactFormContainer"}>
                        <h1 className={"ContactHeader"}>Contact</h1>
                        <form method={"GET"} action={"#"} className={"ContactForm"}>
                            <label For={"Name"} className={"input-label"}>Name</label>
                            <input id={"Name"} type={"text"} className={"input-text"} placeholder={"Your Name"} />
                            <label For={"Email"} className={"input-label"}>Email</label>
                            <input id={"Email"} type={"text"} className={"input-text"} placeholder={"Your Email address"} />
                            <label For={"Phone"} className={"input-label"}>Phone Number</label>
                            <input id={"Phone"} type={"text"} className={"input-text"} placeholder={"Your Phone Number"} />
                            <input type={"submit"} value={"Send"} className={"submit-button"}/>
                        </form>
                    </div>
                    <div className={"AddressContainer"}>
                        <h1>Visit us</h1>
                        <p>New York, NY 10004</p>
                        <p>+1 212-363-3200</p>
                        <Map containerStyle={{position:'relative',width:'auto',height:400}} google={this.props.google} />
                    </div>
                </div>
                <Footer />
            </div>
        );
    }
}

export default GoogleApiWrapper({
    apiKey: "AIzaSyBvNZdSyN4Hde17UNdxpVDMIsfVN58RWqg"
})(MainPage)