import React, { Component } from 'react';

import './main-button-bar.css';

import FacialButton from '../assets/button-facial.png';
import BodyButton from '../assets/button-body.png';
import BeautyButton from '../assets/button-beauty.png';
import ProductButton from '../assets/button-product.png';

class MainButtonBar extends Component {
    render() {
        return(
            <div className={"ButtonBar"}>
                <img src={FacialButton} />
                <img src={BodyButton} />
                <img src={BeautyButton} />
                <img src={ProductButton} />
            </div>
        );
    }
}

export default MainButtonBar;