import React, { Component } from 'react';

import MainImage3 from '../assets/main-image-3.jpg';

import './bestseller-card.css';

class BestSellerCard extends Component {
    render() {
        const style = this.props.style;
        return (
            <a>
                <div style={style} className={"Card"}>
                    <img src={MainImage3} className={"CardImage"}/>
                    <span className={"CardTitle"}>ItemName</span>
                </div>
            </a>
        );
    }
}

export default BestSellerCard;